const galleryPosition = document.querySelector('.gallery');

function galleryItemTemplate(item) {
	return `<li class="gallery-item">
	<a class="gallery-link" href="${item.original}">
		<img 
			class="gallery-image" 
			src="${item.preview}" 
			alt="${item.description}" 
			/>
	</a>
	<div class="caption">${item.description}</div>
</li>`;
};

function galleryTemplate(images) {
	const markup = images.map(galleryItemTemplate).join('\n\n');
	return markup;
}

function render() {
	const markupToRender = galleryTemplate(images);
	galleryPosition.innerHTML = markupToRender;
}

render();

new SimpleLightbox('.gallery a', {
    captionsData: 'alt',
    captionDelay: 250,
});